.big-square-1, .big-square-2, .big-square-3 {
    width: 60px;
    height: 60px;
    border: 2px solid #1cb4ff;
    position: absolute;
    top: calc(50% - 60px);
    left: calc(50% - 30px);
    border-radius: 10%;
    opacity: 0.4;
}

.big-square-1 {
    -webkit-animation:big-square-1 10s linear infinite;
            animation:big-square-1 10s linear infinite;
}
@-webkit-keyframes big-square-1 {
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}
@keyframes big-square-1 {
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

.big-square-2 {
    -webkit-animation:big-square-2 10s linear infinite;
            animation:big-square-2 10s linear infinite;
}
@-webkit-keyframes big-square-2 {
    from {-webkit-transform: rotate(30deg);transform: rotate(30deg);}
    to {-webkit-transform: rotate(390deg);transform: rotate(390deg);}
}
@keyframes big-square-2 {
    from {-webkit-transform: rotate(30deg);transform: rotate(30deg);}
    to {-webkit-transform: rotate(390deg);transform: rotate(390deg);}
}

.big-square-3 {
    -webkit-animation:big-square-3 10s linear infinite;
            animation:big-square-3 10s linear infinite;
}
@-webkit-keyframes big-square-3 {
    from {-webkit-transform: rotate(60deg);transform: rotate(60deg);}
    to {-webkit-transform: rotate(420deg);transform: rotate(420deg);}
}
@keyframes big-square-3 {
    from {-webkit-transform: rotate(60deg);transform: rotate(60deg);}
    to {-webkit-transform: rotate(420deg);transform: rotate(420deg);}
}

.little-square-1, .little-square-2, .little-square-3 {
    width: 30px;
    height: 30px;
    border: 2px solid #1cb4ff;
    position: absolute;
    top: calc(50% - 45px);
    left: calc(50% - 15px);
    border-radius: 10%;
    opacity: .4;
}

.little-square-1 {
    -webkit-animation:little-square-1 10s linear infinite;
            animation:little-square-1 10s linear infinite;
}
@-webkit-keyframes little-square-1 {
    from {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
    to {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}
@keyframes little-square-1 {
    from {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
    to {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}

.little-square-2 {
    -webkit-animation:little-square-2 10s linear infinite;
            animation:little-square-2 10s linear infinite;
}
@-webkit-keyframes little-square-2 {
    from {-webkit-transform: rotate(390deg);transform: rotate(390deg);}
    to {-webkit-transform: rotate(30deg);transform: rotate(30deg);}
}
@keyframes little-square-2 {
    from {-webkit-transform: rotate(390deg);transform: rotate(390deg);}
    to {-webkit-transform: rotate(30deg);transform: rotate(30deg);}
}

.little-square-3 {
    -webkit-animation:little-square-3 10s linear infinite;
            animation:little-square-3 10s linear infinite;
}
@-webkit-keyframes little-square-3 {
    from {-webkit-transform: rotate(420deg);transform: rotate(420deg);}
    to {-webkit-transform: rotate(60deg);transform: rotate(60deg);}
}
@keyframes little-square-3 {
    from {-webkit-transform: rotate(420deg);transform: rotate(420deg);}
    to {-webkit-transform: rotate(60deg);transform: rotate(60deg);}
}



